<template>
  <div class="cell" v-on:click="onClickSelf">
    <template v-if="a">{{text}}</template>
    <template v-else></template>
  </div>
</template>

<script>
export default {
    props:["n"],
    data(){
    return {a:false,text:" "}
  },
  methods:{
      onClickSelf(){
        if(this.text == " "){
          if(this.n % 2 ==0){
            this.text = 'x'
          }
          else { 
            this.text = 'o'
          }
        } 
        else{
          return;
        }
          this.a = true;
          this.$emit("click",this.text);
      }
  }
  
}
</script>

<style>
.cell{
  border: solid black 1px;
  height: 100px;
  width: 100px;
  display: flex;
  justify-content:center;
  align-items: center;
  font-size: 50px; 
}
</style>
