<template>
  <div class="container">
    <div>＃字旗游戏</div>
    <div>目前第{{n}}手</div>
    <div class="chess">
      <div class="row">
        <Cell v-on:click="onClickCell(0,$event)" v-bind:n="n"></Cell>
        <Cell v-on:click="onClickCell(1,$event)" v-bind:n="n"></Cell>
        <Cell v-on:click="onClickCell(2,$event)" v-bind:n="n"></Cell>
      </div>
      <div class="row">
        <Cell v-on:click="onClickCell(3,$event)" v-bind:n="n"></Cell>
        <Cell v-on:click="onClickCell(4,$event)" v-bind:n="n"></Cell>
        <Cell v-on:click="onClickCell(5,$event)" v-bind:n="n"></Cell>
      </div>
      <div class="row">
        <Cell v-on:click="onClickCell(6,$event)" v-bind:n="n"></Cell>
        <Cell v-on:click="onClickCell(7,$event)" v-bind:n="n"></Cell>
        <Cell v-on:click="onClickCell(8,$event)" v-bind:n="n"></Cell>  
      </div>
    </div>
  </div>
</template>

<script>
import Cell from "../src/components/Cell";
export default {
  components:{Cell},
  data(){
    return {
      map:[[null,null,null],[null,null,null],[null,null,null]],
      n:0,
      result:false
    }
  },
  methods:{
    onClickCell(i,text){
      this.map[Math.floor(i/3)][i%3] = text;   
      this.n += 1;
      this.tell();
    },
    tell(){
       for(let i=0;i<3;i++){
        if(this.map[i][0] != null && this.map[i][0] == this.map[i][1] && this.map[i][1] == this.map[i][2]){
          this.result = true; 
          alert(`${this.map[i][0]}赢了`);
          location.reload();
        }
      }
       for(let j=0;j<3;j++){
        if(this.map[0][j] != null && this.map[0][j] == this.map[1][j] && this.map[1][j] == this.map[2][j]){
          this.result = true; 
          alert(`${this.map[0][j]}赢了`);
          location.reload();
        }
      }
      if(this.map[0][0] != null && this.map[0][0] == this.map[1][1] && this.map[1][1] == this.map[2][2]){
        this.result = true;
        alert(`${this.map[0][0]}赢了`);
        location.reload();
      }
      if(this.map[0][2] != null && this.map[0][2] == this.map[1][1] && this.map[1][1] == this.map[2][0]){
        this.result = true;
        alert(`${this.map[0][2]}赢了`);
        location.reload();
      }
    }
  }
}
</script>

<style>
.row{
  display: flex;
}
.container{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

</style>
